.box-card{
  box-shadow: 0px 3px 3px #00000029;
  border-radius: 5px;
  width: 100%;
  margin-top: 10px;
  .box-card-header{
    background: #F6F6F6;
    color: #474747;
    padding: 10px 30px;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    text-align: right;
    .box-card-header-title{
      font-size: 15px;
      color: #404040;
      font-weight: bolder;
    }

    .box-card-header-description{
      font-size: 14px;
    }
  }


  .box-card-content{
    background: white;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;

    .ant-switch-checked {
      margin-left: 10px;
    }
      .inputs{
      display: flex;
      flex-direction: column;
      margin-top: 15px;
      label{
        font-size: 12px;
        color: #555555;
      }

      input.form-control{
        height: 34px;
        border: 1px solid #8D8D8D;
        border-radius: 5px;
        background-color: #FBFBFB;
        color: #878585;
        font-size: 12px;
      }

      textarea.form-control{
        border: 1px solid #8D8D8D;
        border-radius: 5px;
        background-color: #FBFBFB;
        color: #878585;
        font-size: 12px;
      }
    }

    .description{
      color: rgba(39, 30, 112, 0.80);
      font-size: 14px;
      font-weight: bold;
      margin-top: 5px;
    }

    .more-details{
      img{
        margin-left: 10px;
      }
      margin-top: 5px;
      margin-bottom: 10px;
      color: rgba(39, 30, 112, 0.80);
      font-size: 10px;
      font-weight: bold;
    }

    .big-plus-box{
      height: 350px!important;
    }

    .big-plus-box2{
      height: 380px!important;
    }
    .list-box{
      border: 0.3px solid #271E70;
      border-radius: 5px;
      min-height: 290px;
      width: 100%;
      padding: 20px;
      margin: 30px 30px 30px 0px;
      overflow-y: scroll;

      @media screen and (max-width: 600px) {
        margin: 15px;
      }
      ul{
        padding-right: 0px;
        list-style-type: none;
        .images{
          height: 150px;
          width: 100%;
          margin-bottom: 5px;
        }
        li{
          display: flex;
          justify-content: space-between;
          font-size: 12px;
          color: #271E70;
          font-weight: bold;
          border-bottom: 1px solid #271E70;
          min-height: 35px;
          align-items: flex-end;
          //padding-bottom: 5px;
          padding-top: 5px;
          &:hover{
            cursor: pointer;
          }

          .form-control{
            border: 1px solid transparent;
            font-size: 12px;
            color: #271E70;
            font-weight: bold;
            padding-right: 0px;
            height: 25px;
            padding-bottom: 0px;
          }
          input::placeholder{
            font-size: 12px;
            color: #271E70;
          }
          .input-group-text{
            background-color: transparent;
            border: 1px solid transparent;
            padding-left: 0px;
          }
        }
      }

      .title{
        font-size: 12px;
        color: #271E70;
        font-weight: bold;
      }
      .form-control{
        border: 1px solid #271E70;
        border-radius: 5px;
      }
    }

    .list-box-plus{
      border: 0.3px solid #271E70;
      border-radius: 5px;
      height: 290px;
      width: 100%;
      padding: 20px;
      margin: 30px 30px 30px 30px;
      display: flex;
      align-items: center;
      justify-content: center;
      @media screen and (max-width: 600px) {
        margin: 15px;
      }
      &:hover{
        cursor: pointer;
      }
    }

    .big-plus{
      border: 1px dashed #271E70;
      border-radius: 5px;
      width: 100%;
      padding: 20px;
      margin: 0px 30px 30px 30px;
      display: flex;
      align-items: center;
      justify-content: center;

      &:hover{
        cursor: pointer;
      }
      @media screen and (max-width: 600px) {
        margin: 0px 15px 15px 15px;

      }
      }

    .small-margin{
      padding: 10px!important;
    }
    .big-box{
      border: 0.3px solid #707070;
      border-radius: 5px;
      width: 100%;
      padding: 20px;
      margin: 30px 30px 10px 30px;
      @media screen and (max-width: 600px) {
        margin: 15px;
      }
      .chips-items{
        display: flex;
        @media screen and (max-width: 500px) {
          display: contents;
        }
      }
      .chips{
        display: flex;
        border: 1px solid;
        font-size: 14px;
        height: 30px;
        border-radius: 5px;
        align-items: center;
        padding: 10px;
        margin-left: 10px;
        color: #271E70;
        font-weight: bold;
        &:hover{
          cursor: pointer;
        }
        img{
          margin-left: 7px;
        }

        @media screen and (max-width: 500px) {
          width: auto;
          margin-bottom: 5px;
        }
      }
      input::placeholder{
        font-size: 14px;
        color: #271E70;
      }

      textarea::placeholder{
        font-size: 14px;
        color: #271E70;
      }

      textarea{
        margin-top: 10px;
      }
    }


  }
}

.role{
  font-size: 16px;
  color: #271E70;
  margin-right: 10px;
  text-align: right;
}
.save{
  font-size: 18px;
  color:white;
  background: #46A8E1;
  height: 45px;
  width: 100%;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  &:hover{
    cursor: pointer;
  }

  @media screen and (max-width: 600px) {
    margin-top: 15px;
    margin-bottom: 15px;
  }
}
