.sidebar{
  box-shadow: 1px 2px 10px #00000029;
  background: white;
  border-radius: 5px;
  padding: 15px;
  text-align: right;
  list-style-type: none;
  margin-top: 10px;
  width: 100%;
  @media screen and (max-width: 800px) {
    display: none;
  }
  li{
    font-size: 14px;
    height: 40px;
    font-weight: bolder;
    img{
      margin-left: 10px;
    }

    &:hover{
      cursor: pointer;
    }
  }
}

.mobile-sidebar{
  display: none;
  @media screen and (max-width: 800px) {
    display: flex;
    background: white;
    width: 100%;
    height: 50px;
    margin-top: 5px;
    box-shadow: 1px 2px 10px #00000029;
    border-radius: 5px;
    padding-right: 5px;

  }
}

.ant-menu-item{
  padding-right: 0px!important;
  padding-left: 0px!important;
  .mobile-menu-item{
    font-size: 14px;
    //height: 40px;
    font-weight: bolder;
    display: flex;
    padding: 0px 10px;
    img{
      margin-left: 10px;
    }
  }
}
