@font-face {
  font-family: IRANSans-web;
  font-style: normal;
  font-weight: normal;
  src: url('assets/fonts/iranSans/IRANSansWeb.eot');
  src: url('assets/fonts/iranSans/IRANSansWeb.eot?#iefix') format('embedded-opentype'),
  url('assets/fonts/iranSans/IRANSansWeb.woff2') format('woff2'),
  url('assets/fonts/iranSans/IRANSansWeb.woff') format('woff'),
  url('assets/fonts/iranSans/IRANSansWeb.ttf') format('truetype');
}

@font-face {
  font-family: IRANSans-web-bold;
  font-style: normal;
  font-weight: normal;
  src: url('assets/fonts/iranSans/IRANSansWeb_Bold.woff') format('woff')
}

@font-face {
  font-family: IRANSansWeb-FaNum;
  font-style: normal;
  font-weight: normal;
  src: url('assets/fonts/iranSansFa(num)/IRANSansWebFaNum-Medium.woff2') format('woff2'),
  url('assets/fonts/iranSansFa(num)/IRANSansWebFaNum-Medium.woff') format('woff');
}


body, div, table, label, button, span, a, p, legend, input,pre {
  text-decoration: none;
  font-family: IRANSans-web;
}


code {
  text-decoration: none;
  font-family: IRANSans-web;
}
body {
  background-color: #FCFCFC !important;
  overflow-x: hidden;
  direction: rtl;
}

html {
  direction: rtl;
  text-align: right;
}

.fa-text{
  font-family: IRANSansWeb-FaNum;
}


input .form-control:active,.form-control:hover,.form-control {
  outline: none;
}

.form-control:focus{
  box-shadow: unset!important;
}


*{
  outline: none;
}
